import React from "react";
class ToDo extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <li>
                {this.props.value}
                <button onClick={() => this.props.deleteTask(this.props.id)}>
                    <i className="fas fa-trash-alt"></i>
                </button>
            </li>
        );
    }
}

export default ToDo;