import React from 'react';
import Item from './to-do-item';

export default class ToDoList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            taskName: '',
            tasks: []
        }
    }

    myTaskChangeHandler = (event) => {
        this.setState({ taskName: event.target.value })
    }

    addTask = () => {
        if (this.state.taskName.trim() === '')
            return;
        this.state.tasks.push(this.state.taskName);
        this.setState({ taskName: '' });
        console.log(this.state.tasks);
    }

    deleteTask = (id) => {
        let tasks = this.state.tasks.filter((task, index) => index !== id)
        this.setState({ tasks: tasks })
    }

    render() {
        return (
            <div className="container todolist">
                <h1 className="display-1">To Do List</h1>
                <div className="form-group">
                    <button className="btn btn-success" onClick={this.addTask}>
                        <i className="fas fa-plus-circle"></i>
                    </button>
                    <input onChange={this.myTaskChangeHandler} value={this.state.taskName} type="text" className="form-control mt-1" />
                </div>
                <ul className="todolist__list">
                    {
                        this.state.tasks.map((item, index) => {
                            return <Item key={index} value={item} id={index} deleteTask={this.deleteTask} />;
                        })
                    }
                </ul>
            </div >
        );
    }
}